<template>
  <div>
    <div class="m-10 pt-10">
      <span
        class="lg:text-6xl xl:text-6xl md:text-3xl text-2xl dark:text-white"
      >
        Welcome to ArtKonnection</span
      ><br /><br />
      <span class="lg:text-3xl xl:text-3xl md:text-xl text-xl dark:text-white"
        >Amazing Projects are waiting for you!</span
      >
    </div>
    <hr class="border-black dark:border-white m-3" />
    <div class="flex justify-around m-3">
      <model-viewer
        v-for="i in 3"
        :key="i"
        class="m-3 p-3"
        :src="`${URL}/artkonnection/${i}`"
        auto-rotate
      ></model-viewer>
    </div>
    <hr class="border-black dark:border-white m-3" />

    <span class="text-2xl dark:text-white">Art Projects</span>
    <div
      v-for="(item, i) in datas"
      :key="i"
      class="m-10 p-3 border-b-2 border-t-2 h-auto border-black dark:border-white"
    >
      <div class="grid grid-cols-2 pt-5 lg:pt-10">
        <span
          class="text-xl md:text-2xl lg:text-4xl dark:text-white justify-self-start ml-5"
          >{{ item.projectName }} by {{ item.artist }}</span
        >

        <router-link
          :to="'/aboutcollection/' + item.id"
          class="justify-self-end mr-5"
        >
          <p
            class="dark:text-white border-2 w-auto p-1 rounded border-black dark:border-white"
          >
            About Collection
          </p>
        </router-link>
      </div>
      <hr class="border-black dark:border-white mt-3" />

      <Carousel
        :itemsToShow="3"
        :wrapAround="true"
        :autoplay="3000"
        class="flex items-center"
      >
        <Slide v-for="slide in 9" :key="slide">
          <div class="m-3">
            <img
              v-if="!item.notImage"
              :src="`${URL}/assets/${item.id * 1000000 + slide}`"
              alt="Token image"
              @error="item.notImage = true"
            />
            <model-viewer
              v-if="item.notImage"
              :src="`${URL}/assets/${item.id * 1000000 + slide}`"
              auto-rotate
            ></model-viewer>
          </div>
        </Slide>
      </Carousel>
    </div>
  </div>
</template>

<script>
import { Carousel, Slide } from "vue3-carousel";
import { reactive, onMounted, ref } from "vue";
import { CONTRACT_ABI, CONTRACT_ADDRESS } from "@/assets/artKonnection.js";
import Caver from "caver-js";

import "vue3-carousel/dist/carousel.css";

export default {
  components: {
    Carousel,
    Slide,
  },
  setup() {
    let datas = reactive([]);
    const URL = ref(process.env.VUE_APP_SERVER_URL);

    let projectNames = reactive([]);
    let nums = ref(0);
    let caver = null;
    let contract = null;
    onMounted(async () => {
      caver = new Caver(
        window.klaytn._kaikas.isEnabled()
          ? window.klaytn
          : process.env.VUE_APP_RPC_URL
      );
      contract = new caver.klay.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
      _nextProjectId();
    });
    const _projectDetails = async (_projectId) => {
      await contract.methods
        .projectDetails(_projectId)
        .call()
        .then((res) => {
          res.id = _projectId;
          res.notImage = false;
          if (!projectNames.includes(res.projectName) && nums.value < 4) {
            datas.push(res);
            nums.value++;
            projectNames.push(res.projectName);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const _nextProjectId = async () => {
      await contract.methods
        .nextProjectId()
        .call()
        .then((result) => {
          for (let i = 0; i < 10; i++) {
            const _projectId = Math.floor(Math.random() * result);
            _projectDetails(_projectId);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };
    return {
      datas,
      Carousel,
      URL,
      Slide,
    };
  },
};
</script>

<style scoped>
@media (min-width: 640px) {
  model-viewer {
    height: 200px;
  }
}
@media (min-width: 768px) {
  model-viewer {
    height: 300px;
  }
}
@media (min-width: 1024px) {
  model-viewer {
    height: 400px;
  }
}
@media (min-width: 1280px) {
  model-viewer {
    height: 600px;
  }
}

@media (min-width: 640px) {
  img {
    height: 200px;
  }
}
@media (min-width: 768px) {
  img {
    height: 300px;
  }
}
@media (min-width: 1024px) {
  img {
    height: 400px;
  }
}
@media (min-width: 1280px) {
  img {
    height: 600px;
  }
}

